
.error-container{z-index:50;position:fixed;width:100%;height:100%;top:0;bottom:0;left:0;right:0;background-color:#f4f4f4
}
.error-container .error-img{margin:200px auto 40px;width:280px;height:100px;background-image:url(//ming665.github.io/static/img/loadFailed.399fbe8.png);background-repeat:no-repeat;background-size:100%
}
.error-container .error-info{text-align:center;font-size:14px;line-height:14px;margin-bottom:40px;color:#555
}
.error-container .error-button{margin:0 auto;width:140px;height:45px;border:1px #c7c7c7 solid;border-radius:4px;color:#999;font-size:14px;line-height:45px;text-align:center
}

.loading-container{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;position:fixed;z-index:10000;left:0;top:0;width:100%;height:100%;background-color:#f4f4f4
}
.loading-container>div{padding-bottom:60px
}
.loading-container .loading-img{background-image:url(//ming665.github.io/static/img/refresh.00100b5.gif);background-repeat:no-repeat;width:180px;height:60px;background-size:100%;margin-bottom:10px
}
.loading-container .loading-text{font-size:12px;line-height:14px;text-align:center;color:#555
}

.footer-bar{-webkit-box-sizing:border-box;box-sizing:border-box;position:fixed;z-index:10001;bottom:0;left:0;width:100%;height:60px;border-top:1px solid #e7e7e7;background-color:#fff
}
.footer-bar .nav-bar{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row
}
.footer-bar .nav-bar .nav-item{-webkit-box-flex:1;-ms-flex:1;flex:1;font-size:22px;text-align:center;padding-top:10px
}
.footer-bar .nav-bar .nav-item .icon-nav{font-size:22px
}
.footer-bar .nav-bar .nav-item p{font-size:12Px;line-height:12Px;margin-top:4px;color:#757575
}
.footer-bar .nav-bar .nav-item.active{color:#4cc3ff
}
.footer-bar .nav-bar .nav-item.active p{color:#4cc3ff
}
